@import url('https://fonts.googleapis.com/css2?family=Carter+One&family=Noto+Serif+JP:wght@200&display=swap');


.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #0A2239;
  color: #F1F1F2;
  font-family: 'Carter One', cursive;
}

header {
  width: 100%;
  height: auto; 
  background: #031a30;
}

nav {
  position: relative;
  z-index: 1;
  width: 100%;
  height: auto;  
  background-color: #163A5F;
  border-radius: 0 0 20px 20px;
}

main {
  flex-grow: 1;
  background: linear-gradient(to bottom, #031a30, #112B44, #111c25);
  /* padding: 20px; */
}

footer {
  padding: 100px 0 5px 0;
  height: auto;
  background-color: #111c25;  
}

button {
  font-family: 'Carter One', cursive;
}


.activeScrollBtn {
  position: fixed;
  right: 1%;
  bottom: 3%;
  font-size: 35px;
  border: none;
  padding: 10px 10px 0px 10px;
  background: #ffffff7d;
  border-radius: 5px;
  color: #FFC30B;
  transition: transform .4s ease-in-out;
}

.activeScrollBtn:hover {
  transform: translateY(-10px);
}

@media screen and (max-width: 768px){
  .activeScrollBtn {
    right: 2%;
    padding: 5px 5px 0 5px;
    font-size: 25px;
  }
}